import { GetBusinessAccountUnitbyhotel } from '@/api/api_agreement';
export default {
    name: 'OrderDay',
    data() {
        let startTime = util.format(new Date(), 'yyyy-MM-dd');
        return {
            title: '请选择基础房型',
            title_0: '请选择产品房型',
            hotelStyle: [],
