                roomNum: 1,
                startTime,
                diffDay: 1, //入住天数，小时数
                orderSource: '',
                phone: '',
                realAmount: ''
            },
            orderSource: [],
            orderfirstName: '',
            roomNumResult: null,
            opened: false, //第二层
            retShow: true,
            roomNum: '',
            scaleName_0: '',
            proList: [],
            pro_show: false,
            pro_show_0: false,
            freeNum: '',
            type: '日租',
            types: [
                { num: '1', name: '日租' },
                { num: '3', name: '长租' }
            ],
            typeShow: false,
            pay: '请选择',
            pay_show: false,
            options: [],
            delay: false,
