            currentTime: '',
            timeList: ['12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00', '23:59', '整晚保留'],
            weekList: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
            timeShow: false,
            computerDiffDay: '',
            ComeTime: false,
            radio: '1',
