            orderTypeAll: [
                { num: '1', name: '日租' },
                { num: 2, name: '小时房' }
            ],
            orderTypeChoosed: '日租',
            hours_left: null,
            housrTime: null,
            minHour: 1,
            maxHour: 23,
            minHourRight: 1,
            maxHourRight: 23,
            minMinuteRight: 0,
            orderTypeChoosed_type: '1',
            arrLeft: [],
            housrTime_l: '09:00',
            housrTime_r: '18:00'
        };
    },
    created() {
        this.getOrderType();
        this.hotelId = this.$route.query.hotelId;
    },
    mounted() {
        this.$store.commit('setHeader', '订单同步-日租');
        var day1 = new Date();
        day1.setTime(day1.getTime() - 24 * 60 * 60 * 1000);
        this.cu_time = day1.getFullYear() + '-' + (day1.getMonth() + 1 < 10 ? '0' + (day1.getMonth() + 1) : day1.getMonth() + 1) + '-' + (day1.getDate() < 10 ? '0' + day1.getDate() : day1.getDate());
    },
    computed: {
        endTime() {
            if (!this.orderInfo.startTime) {
                return null;
            }
            let [y, m, d] = this.orderInfo.startTime.split('-');
            let t = new Date(y, Number(m) - 1, d);
            let t1 = new Date(t.getTime() + this.orderInfo.diffDay * (24 * 3600 * 1000));
            return util.format(t1, 'yyyy-MM-dd');
        },
        getNum: function () {
            return this.orderInfo.roomNum;
        },
        totlePrice() {
            var sum = null;
            sum = this.dayList.reduce((total, currentValue, currentIndex, arr) => {
                return total + Number(currentValue.price);
            }, 0);
            console.log('input disabled totlePrice');
            return Number(sum * this.orderInfo.roomNum).toFixed(2);
        }
    },
    methods: {
        confirmHoursLeft(e) {
            this.arrLeft = e.split(':');
            this.hours_startTime = dayjs(this.orderInfo.startTime).format('YYYY-MM-DD') + ' ' + e + ':00';
            this.housrTime_l = e;
            this.hours_left_show = false;
            this.hours_right_show = true;
            this.minHourRight = this.arrLeft[0];
            this.minMinuteRight = this.arrLeft[1];
        },
        confirmHoursRight(e) {
            this.hours_endTime = dayjs(this.orderInfo.startTime).format('YYYY-MM-DD') + ' ' + e + ':00';
            this.housrTime_r = e;
            this.housrTime = this.housrTime_l + '-' + this.housrTime_r;
            this.hours_right_show = false;
            let hours = dayjs(this.hours_endTime).diff(dayjs(this.hours_startTime), 'hour');
            if (hours < this.orderInfo.diffDay) {
                this.orderInfo.diffDay = hours;
            }
            this.title = '请选择基础房型';
            this.title_0 = '请选择产品房型';
        },
        confirmHoursLong(num) {
            let hours = dayjs(this.hours_endTime).diff(dayjs(this.hours_startTime), 'hour');
            if (num > hours) {
                this.housrTime = '请选择有效时段';
            }
            this.orderInfo.diffDay = num;
            this.hoursLong_show = false;
        },
        confirmOrderType(item) {
            this.orderTypeChoosed = item.name;
            this.orderTypeChoosed_type = item.num;
            this.orderInfo.startTime = dayjs().format('YYYY-MM-DD');
            if (this.orderTypeChoosed_type == '2') {
                this.orderInfo.diffDay = '4';
                this.hours_startTime = dayjs(this.orderInfo.startTime).format('YYYY-MM-DD') + ' ' + '09:' + '00:00';
                this.hours_endTime = dayjs(this.orderInfo.startTime).format('YYYY-MM-DD') + ' ' + '18:' + '00:00';
                this.housrTime = '09:00' + '-' + '18:00';
                this.title = '请选择基础房型';
                this.title_0 = '请选择产品房型';
                this.$store.commit('setHeader', '订单同步-时租');
            } else {
                this.orderInfo.diffDay = '1';
            }
            this.orderType_show = false;
        },
        //表单选择
        onConfirm(val) {
            this.billCycle = val + ' ' + '＞';
            this.payeeAccountId = this.columnsAll.find((item) => item.accountName == val).id;
            this.showPicker = false;
        },
        // 选择入住时间
        chooseComeTime(num) {
            this.title = '请选择基础房型';
            this.title_0 = '请选择产品房型';
            this.ComeTime = false;
            this.orderInfo.diffDay = num;
        },
        // 按日历房价
        getDateStr(AddDayCount) {
            var dd = new Date();
            dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期
            var y = dd.getFullYear();
            var m = dd.getMonth() + 1 < 10 ? '0' + (dd.getMonth() + 1) : dd.getMonth() + 1; //获取当前月份的日期，不足10补0
            var d = dd.getDate() < 10 ? '0' + dd.getDate() : dd.getDate(); //获取当前几号，不足10补0
            return y + '-' + m + '-' + d;
        },
        time() {
            this.title = '请选择基础房型';
            this.title_0 = '请选择产品房型';
            this.id = null;
        },
        priceTwoNum(value, num) {
            if (num == '3') {
                for (let i = 0; i < this.dayList.length; i++) {
                    let ele = this.dayList[i];
                    if (value == i) {
                        ele.price = ele.price.toString();
                        ele.price = ele.price.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
                        ele.price = ele.price.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
                        ele.price = ele.price.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
                        ele.price = ele.price.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
                        if (ele.price.indexOf('.') < 0 && ele.price != '') {
                            //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                            ele.price = parseFloat(ele.price);
                        }
                    }
                }
            } else {
                value = value.toString();
                value = value.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
                value = value.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
                value = value.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
                value = value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
                if (value.indexOf('.') < 0 && value != '') {
                    //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                    value = parseFloat(value);
                }
                if (num == '1') {
                    this.totleAmount = value;
                } else if (num == '2') {
                    this.totlePrice = value;
                }
            }
        },
        // 查询订单来源
        getOrderType() {
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let arr = [];
                for (var i = 0; i < res.data.length; i++) {
                    if (res.data[i] != undefined) {
                        var obj = {
                            source: res.data[i].dictSort,
                            name: res.data[i].dictLabel,
                            imgStatu: false
                        };
                        arr.push(obj);
                    }
                }
                arr = arr.filter((item) => {
                    return item.source != 1;
                });
                this.orderInfo.orderSource = arr[0].source;
                this.orderfirstName = arr[0].name;
                arr[0].imgStatu = true;
                this.orderSource = arr;
            });
        },
        // 选择房型
        hotelStyleChooseFunc(item, index) {
            this.scaleName_0 = item.scaleName;
            this.roomNumberChoosed = 0;
            this.hotelStyleChoosed = true;
            this.hotelStyleChooseIndex = index;
            this.hotelStyleChoose = false;
            this.id = this.hotelStyle[index].id;
            //明天的时间
            let to_time = this.orderInfo.startTime;
            let day3 = new Date(to_time);
            day3.setTime(day3.getTime() + 24 * 60 * 60 * 1000);
            var s1 = day3.getFullYear() + '-' + (day3.getMonth() + 1 < 10 ? '0' + (day3.getMonth() + 1) : day3.getMonth() + 1) + '-' + (day3.getDate() < 10 ? '0' + day3.getDate() : day3.getDate());
            this.to_time = s1;
            this.disabledBtnSearch = false; // 重置查询
        },
        // 查询基础房型
        btnPro_0() {
            if (this.orderTypeChoosed_type == '2' && !this.hours_startTime) {
                Toast('请先选择有效时段');
                return;
            }
            this.title_0 = '请选择产品房型';
            if (this.title == '请选择基础房型') {
                if (this.orderTypeChoosed_type == '2') {
                    this.hours_startTime = dayjs(this.orderInfo.startTime).format('YYYY-MM-DD') + ' ' + this.housrTime_l + ':00';
                    this.hours_endTime = dayjs(this.orderInfo.startTime).format('YYYY-MM-DD') + ' ' + this.housrTime_r + ':00';
                } else {
                    this.endtime = dayjs(this.orderInfo.startTime).add(this.orderInfo.diffDay, 'day').format('YYYY-MM-DD');
                }
                let data = {
                    scaleType: this.orderTypeChoosed_type == '2' ? '3' : '1',
                    startTime: this.orderTypeChoosed_type == '1' ? this.orderInfo.startTime : this.hours_startTime,
                    endTime: this.orderTypeChoosed_type == '1' ? this.endtime : this.hours_endTime
                };
                GetBusinessRoomscaleStateByhotel(this.hotelId, data).done((res) => {
                    if (res.data.length == 0) {
                        Toast('暂无基础房型');
                        return;
                    }
                    if (res.data.length > 0) {
                        for (let i = 0; i < res.data.length; i++) {
                            res.data[i].imgStatu = false;
                        }
                        this.pro_show_0 = true;
                    }
                    this.hotelStyle = res.data.sort(function (a, b) {
                        return b.freeNum - a.freeNum;
                    });
                });
            } else {
                this.pro_show_0 = true;
                this.hotelStyle = this.newHotelStyle;
            }
        },
        // 选择时间
        getHour(value) {
            this.reserveTime = value;
            this.timeShow = false;
        },
        //  取消选择
        cancel() {
            this.timeShow = false;
        },
        // 选择基础房型
        choose_pro_0(index, item) {
            this.title = item.scaleName;
            for (let i = 0; i < this.hotelStyle.length; i++) {
                if (index == i) {
                    this.hotelStyle[i].imgStatu = true;
                } else {
                    this.hotelStyle[i].imgStatu = false;
                }
            }
            this.newHotelStyle = this.hotelStyle;
            this.id = item.id;
            this.d_freeNum = item.freeNum;
            this.pro_show_0 = false;
            if (this.orderTypeChoosed_type == '1' || (this.orderTypeChoosed_type == '2' && item.freeNum > '0')) {
                this.btnPro();
            } else {
                Toast({
                    message: '暂无产品房型',
                    duration: 900
                });
            }
        },
        // 查询产品房型
        btnPro() {
            if (this.title_0 == '请选择产品房型') {
                if (this.id == null || this.title == '请选择基础房型') {
                    Toast({
                        message: '请先选择基础房型',
                        duration: 900
                    });
                    return false;
                }
            }
            if (this.orderTypeChoosed_type == '1') {
                let data = {
                    startTime: this.orderInfo.startTime,
                    endTime: this.endtime,
                    hotelId: this.hotelId
                };
                GetBusinessVirtualRoomscaleStateByroomscale(this.id, data).done((res) => {
                    this.dealPro(res.data);
                });
            } else {
                let data = {
                    roomScaleId: this.id,
                    scaleType: this.orderTypeChoosed_type == '2' ? '3' : '1'
                };
                GetBusinessvirtualRoomscaleScaleTypeByRoomScale(data).done((res) => {
                    this.dealPro(res.data);
                });
            }
        },
        dealPro(proData) {
            if (this.orderTypeChoosed_type == '1') {
                this.proList = proData.filter((item) => item.freeNum);
            } else {
                this.proList = proData;
            }
            if (this.proList.length == 0) {
                this.newProList = [];
                Toast({
                    message: '暂无产品房型',
                    duration: 900
                });
                return false;
            } else if (this.proList.length == 1) {
                this.title_0 = this.proList[0].scaleName;
                this.virtualRoomScaleId = this.proList[0].id;
                this.freeNum = this.orderTypeChoosed_type == '1' ? this.proList[0].freeNum : this.d_freeNum;
                this.deposit_Price = this.proList[0].deposit;
                this.newProList = [];
            } else {
                if (this.newProList.length > 0) {
                    let newProList = this.newProList.map((item) => item.scaleName);
                    let proList = this.proList.map((item) => item.scaleName);
                    if (JSON.stringify(newProList) == JSON.stringify(proList)) {
                        this.proList = this.newProList;
                    } else {
                        for (let i = 0; i < this.proList.length; i++) {
                            this.proList[i].imgStatu = false;
                        }
                    }
                }
                this.pro_show = true;
            }
        },
        // 选择产品房型
        choose_pro(index, item) {
            this.scaleType = item.scaleType;
            this.title_0 = item.scaleName;
            for (let i = 0; i < this.proList.length; i++) {
                if (index == i) {
                    this.proList[i].imgStatu = true;
                } else {
                    this.proList[i].imgStatu = false;
                }
            }
            this.newProList = this.proList;
            this.deposit_Price = item.deposit;
            this.virtualRoomScaleId = item.id;
            this.freeNum = item.freeNum;
            if (this.freeNum <= 0) {
                Toast({
                    message: '产品房型间不足',
                    duration: 900
                });
                this.title_0 = '';
                false;
            }
            this.pro_show = false;
        },
        // 查询可用房间数量
        getUserRooms() {
            let hotelId = cookie.geCookie('currentID');
            let params = {
                hotelId: hotelId,
                startDate: this.orderInfo.startTime,
                endDate: this.endTime
            };
            GetBusinessRoomFreeNum(this.id, params).done((res) => {
                if (res.data.length == 0) {
                    Toast({
                        message: '当前房型无可选房间,请重新选择',
                        duration: 2000
                    });
                }
                this.disabledBtnSearch = true;
                this.roomNum = res.data;
            });
        },
        // 选择入住天数
        choose_num(num) {
            this.orderInfo.roomNum = num;
            this.numList = false;
        },
        // 支付方式
        getPayment() {
            GetBusinessHotelPayeeAccountByhotel({ hotelId: this.$route.query.hotelId }).then((res) => {
                this.columnsAll = res.data.filter((item) => item.payment >= '5' || item.account);
                this.columns = res.data.filter((item) => item.payment >= '5' || item.account).map((item) => item.accountName);
                this.columnsAll.every((item) => {
                    if (this.orderInfo.orderSource == item.orderSource && item.origin == '1') {
                        this.billCycle = item.accountName + ' ' + '＞';
                        this.payeeAccountId = item.id;
                        return false;
                    } else {
                        this.billCycle = '请选择支付方式 >';
                        this.payeeAccountId = '';
                        return true;
                    }
                });
            });
        },
        // 选择支付方式
        choose_pay(item) {
            this.pay = item.label;
            this.pay_value = item.value;
            this.pay_show = false;
        },
        // 延时退房
        btndelay() {
            this.delay = !this.delay;
        },
        // 到店付
        btnpayTime() {
            this.payTimeType = !this.payTimeType;
            if (this.timeList.indexOf('18:00') > -1) {
                this.currentTime = '18:00';
            } else {
                this.currentTime = this.timeList[0];
            }
        },
        // 选择最迟到店时间
        chooseLateTime(time) {
            this.timeShow = false;
            this.currentTime = time;
        },
        // 免押金入住
        btndeposit() {
            this.deposit = !this.deposit;
        },
        // 重置查询or查询
        findRoomOrderableHandle(roomNumResult) {
            if (this.orderTypeChoosed_type == '1' && this.orderInfo.startTime < this.cu_time) {
                Toast({
                    message: '所选的入住时间有误，请重新选择',
                    duration: 1000
                });
                return false;
            }
            if ((this.orderTypeChoosed_type == '1' && !this.orderInfo.diffDay) || !/^[1-9]\d*$/.test(this.orderInfo.diffDay)) {
                Toast({
                    message: '请正确选择入住天数',
                    duration: 900
                });
                this.orderInfo.diffDay = '';
                return false;
            } else {
                let chooseDay = dayjs(this.orderInfo.startTime);
                for (let i = 0; i < this.orderInfo.diffDay; i++) {
                    let currentDay = chooseDay.add(i, 'day');
                    let obj = {};
                    obj.xs_time = dayjs(currentDay).format('MM月DD日');
                    obj.time = dayjs(currentDay).format('YYYY-MM-DD');
                    obj.week = dayjs(currentDay).day();
                    obj.price = null;
                    this.dayList.push(obj);
                }
            }
            if (this.orderTypeChoosed_type == '2' && this.housrTime == '请选择有效时段') {
                Toast({
                    message: '请选择有效时段',
                    duration: 900
                });
                return false;
            }
            if (this.orderTypeChoosed_type == '2' && dayjs().unix() > dayjs(this.hours_endTime).unix()) {
                Toast({
                    message: '有效时段有误，请重新选择',
                    duration: 900
                });
                return false;
            }
            if (this.title == '请选择基础房型') {
                Toast({
                    message: '请正确选择基础房型',
                    duration: 900
                });
                return false;
            }
            if (this.title_0 == '请选择产品房型') {
                Toast({
                    message: '请正确选择产品房型',
                    duration: 900
                });
                return false;
            }
            if (this.roomNumResult) {
                this.roomNumResult = null;
                return false;
            }
            this.endtime = dayjs(this.orderInfo.startTime).add(this.orderInfo.diffDay, 'day').format('YYYY-MM-DD');
            this.computerDiffDay = dayjs(this.endtime).diff(dayjs(this.orderInfo.startTime), 'day');
            this.getPayment();
            this.roomNumResult = true;
            if (this.orderInfo.startTime == dayjs().format('YYYY-MM-DD')) {
                // 当天
                this.timeList = this.timeList.filter((item) => item > dayjs().format('HH:mm'));
            }
        },
        // 根据入住天日期取产品房型房价
        getDayPrice() {
            if (!this.virtualRoomScaleId) {
                return;
            }
            const days = dayjs(this.orderInfo.startTime);
            let endtime = days.add(this.orderInfo.diffDay, 'day');
            this.endtime = endtime.format('YYYY-MM-DD');
            let num = dayjs(this.endtime).diff(dayjs(days), 'day');
            let data = {
                startTime: this.orderInfo.startTime,
                diffDay: num
            };
            GetBusinessVirtualRoomscaleDaypriceDate(this.virtualRoomScaleId, data).done((res) => {
                this.dayList = res.data;
                let arr = [];
                for (const k in this.dayList) {
                    let obj = {};
                    obj.time = k;
                    obj.price = 0;
                    arr.push(obj);
                }
                this.dayList = arr.sort((a, b) => {
                    return Date.parse(b.time) - Date.parse(a.time);
                });
            });
        },
        sortObj(obj) {
            var keysArr = Object.keys(obj).sort();
            var sortObj = {};
            for (var i in keysArr) {
                sortObj[keysArr[i]] = encodeURIComponent(obj[keysArr[i]]);
            }
            return sortObj;
        },
        findRoomOrderable() {
            let params = {
                hotelId: this.hotelId,
                startDate: this.orderInfo.startTime,
                endDate: this.endTime
            };
            GetBusinessRoomFreeNum(this.id, params).done((res) => {
                this.roomNum = res.data;
            });
        },
        // 确定同步-2
        orderPostHandle(num) {
            if (!this.orderInfo.externalOrderSn || this.orderInfo.externalOrderSn.length > 6) {
                Toast({
                    message: '请正确输入订单编码后6位',
                    duration: 900
                });
                this.orderInfo.externalOrderSn = '';
                return false;
            }
            if (!this.orderInfo.roomUser) {
                Toast({
                    message: '请正确输入姓名',
                    duration: 900
                });
                this.orderInfo.roomUser = '';
                return false;
            }
            // 已验证191,181手机号
            if (this.orderInfo.phone && !/^1[3-9]\d{9}$/.test(this.orderInfo.phone)) {
                Toast({
                    message: '请正确输入手机号',
                    duration: 900
                });
                this.orderInfo.phone = '';
                return false;
            }
            this.disabledBtnSearch = false;
            this.orderPost(num);
        },
        // 同步订单
        orderPost(num) {
            if (this.payTimeType == false) {
                this.currentTime = '';
            } else {
                this.currentTime = this.currentTime == '整晚保留' ? '' : this.currentTime;
            }
            if (this.radio != 1) {
                let obj = {};
                this.dayList.forEach((item) => {
                    obj[item.time] = item.price || 0 + '';
                });
                this.dayStr = JSON.stringify(obj);
            }
            this.roomNumResult != null;
            this.orderInfo.realAmount = this.radio == 1 || this.orderTypeChoosed_type == '2' ? this.totleAmount : '';
            this.orderInfo.startTime = this.orderTypeChoosed_type == '2' ? this.hours_startTime : this.orderInfo.startTime;
            this.orderInfoMore = Object.assign(
                {
                    hotelId: this.hotelId,
                    roomScaleId: this.id,
                    virtualRoomScaleId: this.virtualRoomScaleId,
                    orderType: this.orderTypeChoosed_type,
                    delayTime: this.delay == false ? '0' : '1',
                    deposit: this.deposit == false ? this.deposit_Price : 0,
                    reserveTime: this.currentTime,
                    payTimeType: this.payTimeType == false ? 1 : 2,
                    priceRecord: this.radio != 1 ? this.dayStr : '',
                    payeeAccountId: this.payTimeType == false ? this.payeeAccountId : '',
                    accountUnitId: this.accountUnitId,
                    endTime: this.orderTypeChoosed_type == '2' ? this.hours_endTime : ''
                },
                this.orderInfo
            );
            if (!this.payeeAccountId) {
                Toast({
                    duration: 900,
                    message: '请选择收款账户'
                });
                return;
            }
            PostBusinessOrderExternalSyn(this.orderInfoMore).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                if (num == 1) {
                    this.roomNumResult = null;
                } else {
                    localStorage.removeItem('ordermonth');
                    this.$router.replace({
                        name: 'OrderLive',
                        query: {
                            orderday: res.data
                        }
                    });
                    localStorage.setItem('orderday', JSON.stringify(res.data));
                }
                this.title = '请选择基础房型';
                this.title_0 = '请选择产品房型';
                this.value = '0';
                this.delay = false;
                this.orderInfo = {
                    externalOrderSn: null,
                    roomUser: '',
                    roomNum: 1,
                    phone: '',
                    startTime: dayjs().format('YYYY-MM-DD'),
                    diffDay: 1, //入住天数,
                    orderSource: ''
                };
                this.delay = false;
                this.deposit = false;
                this.payTimeType = false;
                this.customerType = '1';
                this.orderTypeChoosed = '日租';
                this.orderTypeChoosed_type = '1';
                this.getOrderType();
            });
        },
        // 用户来源
        orderSourceChooseFunc(index, item) {
            this.orderfirstName = item.name;
            this.orderInfo.orderSource = item.source;
            this.getPayment();
            for (let i = 0; i < this.orderSource.length; i++) {
                if (index == i) {
                    this.orderSource[i].imgStatu = true;
                } else {
                    this.orderSource[i].imgStatu = false;
                }
            }
            this.orderSourceChoose = false;
        },
        // 客户类型
        getCustomerType() {
            GetBusinessAccountUnitbyhotel({ hotelId: this.hotelId }).done((res) => {
                this.customerTypesAll = res.data;
                this.customerTypes = res.data.map((item) => item.cooperationName);
                this.accountUnitId = this.customerTypesAll[0].id;
                this.billCycleType = this.customerTypesAll[0].cooperationName + ' ' + '＞';
            });
        },
        // 选择客户类型
        onConfirmTypes(val) {
            this.billCycleType = val + ' ' + '＞';
            this.accountUnitId = this.customerTypesAll.find((item) => item.cooperationName == val).id;
            this.typeShowPicker = false;
        }
    },
    watch: {
        customerType(neww) {
            if (neww == 2) {
                this.getCustomerType();
            }
        }
    }
};
</script>
